@charset "UTF-8";
@font-face {
  font-family: "Swiss 721 Bold";
  src: url("/src/assets/fonts/swiss-721-bold.otf");
}
@font-face {
  font-family: "Swiss 721 Heavy";
  src: url("/src/assets/fonts/swiss-721-heavy.otf");
}
@font-face {
  font-family: "Swiss 721 Light";
  src: url("/src/assets/fonts/swiss-721-light.otf");
}
@font-face {
  font-family: "Swiss 721 Medium";
  src: url("/src/assets/fonts/swiss-721-medium.otf");
}
@font-face {
  font-family: "Swiss 721 Roman";
  src: url("/src/assets/fonts/swiss-721-roman.otf");
}
*,
*:after,
*:before {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-decoration: none;
}

body {
  font-size: 16px;
  font-family: "Swiss 721 Light";
}

html .container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 768px) {
  html .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  html .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  html .container {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  html .container {
    max-width: 1320px;
  }
}
body.breakup {
  overflow: hidden;
}

.scroll {
  overflow: auto;
}

div.disabled {
  display: none;
}

div.overlay {
  z-index: 100;
  display: none;
  background-color: rgba(33, 33, 33, 0.8);
  color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  transition: 0.1s ease-in-out;
}

div.overlay.appear {
  display: block !important;
}

.breadcrumbs a {
  color: #000;
  font-size: 0.875em;
}
.breadcrumbs a:after {
  content: "›";
  margin: 0 10px;
}
.breadcrumbs a:last-child {
  font-family: "Swiss 721 Medium";
  cursor: default;
}
.breadcrumbs a:last-child:after {
  content: "";
}

header.header div.desktop {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0;
}
header.header div.desktop div.hamburger {
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}
header.header div.desktop div.hamburger svg rect {
  stroke: #FF0000;
  transition: 0.2s ease-in-out;
}
header.header div.desktop div.hamburger:hover svg rect {
  stroke: #A30810;
}
header.header div.desktop div.hamburger strong {
  color: #000000;
  padding: 0 10px;
  font-size: 0.9375em;
}

div.desktop div.search {
  width: 40%;
  display: grid;
  grid-template-columns: 1fr auto;
  align-items: center;
}
div.desktop div.search a {
  width: 50px;
  text-align: center;
}
div.desktop div.search a svg path {
  transition: 0.3s ease-in-out;
}
div.desktop div.search:hover a svg path {
  transition: 0.2s ease-in-out;
  fill: #A30810;
}
div.desktop div.search input {
  width: 100%;
  height: 40px;
  border-radius: 30px;
  padding: 0 15px;
  outline: 0;
  border: none;
  font-family: "Swiss 721 Roman";
  background-color: rgba(96, 93, 92, 0.2);
  transition: 0.2s ease-in-out;
}
div.desktop div.search input:focus {
  background-color: rgba(96, 93, 92, 0.3);
}

div.desktop div.login a {
  display: flex;
  flex-direction: row;
  align-items: center;
}
div.desktop div.login a svg path {
  transition: 0.3s ease-in-out;
}
div.desktop div.login a:hover svg path {
  stroke: #A30810;
}
div.desktop div.login strong span {
  display: block;
  margin: 0 10px;
  text-transform: capitalize;
  font-size: 0.9375em;
  color: #000000;
}

div.desktop div.cart .wrap-cart {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #A30810;
  border: none;
  border-radius: 300px;
  padding: 10px;
}
div.desktop div.cart div.basket {
  position: relative;
  cursor: pointer;
}
div.desktop div.cart div.basket strong {
  display: block;
  width: 16px;
  height: 16px;
  text-align: center;
  font-size: 0.75em;
  font-family: "Swiss 721 Bold";
  border-radius: 30px;
  color: #A30810;
  background-color: #FFFFFF;
  position: absolute;
  top: 8px;
  left: 25px;
  user-select: none;
}
div.desktop div.cart span.frail {
  display: block;
  font-size: 0.75em;
  font-family: "Swiss 721 Light";
  color: #FFFFFF;
  padding: 0 10px;
  user-select: none;
}
div.desktop div.cart div.total strong {
  display: block;
  font-size: 0.875em;
  color: #FFFFFF;
  padding: 0 10px;
  user-select: none;
}

div.desktop div.menu {
  z-index: 110;
  width: 100%;
  height: 100vh;
  position: absolute;
  right: 0;
  top: 0;
  transform: translateY(-999px);
  transition: 0.3s ease-in-out;
}
div.desktop div.menu div.dropdown {
  display: grid;
  grid-template-columns: 1fr 20%;
  width: 100%;
  padding: 30px;
  background-color: #FFFFFF;
  position: absolute;
  top: 122px;
  left: 0;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  height: 400px;
  overflow: auto;
}
div.desktop div.menu div.dropdown div.items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
div.desktop div.menu div.dropdown div.items div.item header h2 {
  color: #FF0000;
  text-transform: uppercase;
  font-family: "Swiss 721 Bold";
  font-size: 1em;
  margin: 0 0 5px 0;
}
div.desktop div.menu div.dropdown div.items div.item ul.parent {
  list-style-type: none;
}
div.desktop div.menu div.dropdown div.items div.item ul.parent li.child {
  text-align: left;
}
div.desktop div.menu div.dropdown div.items div.item ul.parent li.child a {
  font-size: 0.875em;
  line-height: 1.75em;
  color: #605D5C;
  transition: 0.2s ease-in-out;
}
div.desktop div.menu div.dropdown div.items div.item ul.parent li.child a:hover {
  color: #FF0000;
}
div.desktop div.menu div.dropdown div.items div.item:not(:nth-child(1),
:nth-child(4),
:nth-child(5),
:nth-child(8)) {
  margin: 0 15px;
}
div.desktop div.menu div.dropdown div.items div.item:nth-child(n-4) {
  margin-bottom: 10px;
}
div.desktop div.menu div.dropdown div.items div.item:nth-child(n+5) {
  margin-top: 10px;
}
div.desktop div.menu div.detached {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
div.desktop div.menu div.detached a img {
  width: 100%;
}

header.header div.mobile {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 30px 15px;
}
header.header div.mobile div.hamburger {
  text-align: left;
  width: 25%;
  cursor: pointer;
}
header.header div.mobile div.hamburger svg rect {
  stroke: #FF0000;
  transition: 0.2s ease-in-out;
}
header.header div.mobile div.hamburger:hover svg rect {
  stroke: #A30810;
}
header.header div.mobile div.hamburger strong {
  color: #000000;
  padding: 0 10px;
  font-size: 0.9375em;
}

div.mobile div.logo {
  width: 50%;
}
div.mobile div.logo a img {
  width: 100%;
}

div.mobile div.cart {
  width: 25%;
  text-align: right;
  background-color: transparent;
  border: none;
  border-radius: none;
}
div.mobile div.cart div.basket {
  position: relative;
  cursor: pointer;
  display: inline-block;
}
div.mobile div.cart div.basket svg {
  fill: #A30810;
}
div.mobile div.cart div.basket strong {
  display: block;
  width: 16px;
  height: 16px;
  text-align: center;
  font-size: 0.75em;
  font-family: "Swiss 721 Bold";
  border-radius: 30px;
  color: #FFFFFF;
  background-color: #A30810;
  position: absolute;
  top: 8px;
  right: 0;
}
div.mobile div.cart div.basket span {
  display: block;
  font-size: 0.75em;
  font-family: "Swiss 721 Light";
  color: #FFFFFF;
  padding: 0 10px;
}
div.mobile div.cart div.basket div.total strong {
  display: block;
  font-size: 0.875em;
  color: #FFFFFF;
  padding: 0 10px;
}

div.mobile div.menu {
  z-index: 110;
  width: 80vw;
  height: 100vh;
  position: absolute;
  left: 0;
  top: 0;
  transform: translateX(-999px);
  transition: 0.3s ease-in-out;
}
div.mobile div.menu div.dropdown {
  display: block;
  width: 80vw;
  height: 100vh;
  background-color: #FFFFFF;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 0px;
  position: relative;
}
div.mobile div.menu div.dropdown div.info-menu {
  height: 45px;
  line-height: 45px;
  text-align: left;
  margin-left: 20px;
}
div.mobile div.menu div.dropdown div.info-menu strong {
  display: inline-block;
  text-transform: inherit;
}
div.mobile div.menu div.dropdown div.items {
  display: flex;
  flex-direction: column;
  padding: 20px;
}
div.mobile div.menu div.dropdown div.items div.item header {
  width: 100%;
  text-align: left;
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
}
div.mobile div.menu div.dropdown div.items div.item header h2 {
  color: #FF0000;
  text-transform: uppercase;
  font-family: "Swiss 721 Bold";
  font-size: 1em;
}
div.mobile div.menu div.dropdown div.items div.item header svg {
  width: 10px;
  transition: 0.4s ease-in-out;
}
div.mobile div.menu div.dropdown div.items div.item header svg path {
  stroke: #FF0000;
}
div.mobile div.menu div.dropdown div.items div.item header svg.down {
  transform: rotate(180deg);
}
div.mobile div.menu div.dropdown div.items div.item ul.parent {
  list-style-type: none;
  display: none;
}
div.mobile div.menu div.dropdown div.items div.item ul.parent li.child {
  text-align: left;
}
div.mobile div.menu div.dropdown div.items div.item ul.parent li.child a {
  font-size: 0.875em;
  line-height: 1.75em;
  color: #605D5C;
  transition: 0.2s ease-in-out;
}
div.mobile div.menu div.dropdown div.items div.item ul.parent.down {
  display: block;
}
div.mobile div.menu div.dropdown div.items div.item:not(:last-child) {
  margin: 0 0 25px 0;
}
div.mobile div.menu div.dropdown div.detached {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
div.mobile div.menu div.dropdown div.detached a img {
  width: 100%;
}

div.mobile .access {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
div.mobile .access ul.panel {
  background: #FF0000;
  list-style: none;
}
div.mobile .access ul.panel li {
  padding: 15px 10px;
  border-bottom: thin solid rgba(163, 8, 16, 0.2);
}
div.mobile .access ul.panel li a {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  text-transform: capitalize;
}
div.mobile .access ul.panel li strong {
  display: inline;
  color: #FFFFFF;
}

div.desktop div.menu.down {
  transform: translateY(0);
}

div.mobile div.menu.down {
  transform: translateX(0);
}

div.desktop div.minicart {
  display: none;
  width: 400px;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  background: #FFFFFF;
  z-index: 11;
  box-shadow: 0 5px 6px 0 rgba(0, 0, 0, 0.2);
  transform: translateX(999px);
  transition: 0.3s ease-in-out;
}
div.desktop div.minicart .close-minicart {
  position: absolute;
  top: 50%;
  transform: translateX(999px);
  width: 80px;
  height: 50px;
  text-align: center;
  display: table;
  background: #A30810;
  z-index: -1;
  box-shadow: 0 3px 16px 0 rgba(0, 0, 0, 0.12);
}
div.desktop div.minicart .close-minicart strong {
  cursor: pointer;
  display: table-cell;
  text-align: left;
  vertical-align: middle;
  color: #FFFFFF;
  box-shadow: 0 5px 6px 0 rgba(0, 0, 0, 0.2);
  padding-left: 22px;
}
div.desktop div.minicart .close-minicart:hover {
  background: #000000;
}
div.desktop div.minicart .close-minicart.down {
  transform: translateX(-50px);
  transition: 0.6s ease-in-out;
}
div.desktop div.minicart .close-minicart.down:hover {
  transform: translateX(-60px);
  transition: 0.2s ease-in-out;
}
div.desktop div.minicart .main-content div.inventory {
  overflow: auto;
  padding: 15px;
  height: calc(100% - 236px);
}

.desktop div.minicart.down {
  transform: translateX(0);
}

.mobile div.minicart.down {
  transform: translateX(0);
}

div.minicart {
  z-index: 90;
}
div.minicart .minicart-content {
  width: 100%;
  height: 100%;
  background: #FFFFFF;
}
div.minicart .purchase {
  position: absolute;
  left: 0;
  width: 100%;
  bottom: 0;
  padding: 15px;
  background: #FFFFFF;
}
div.minicart .purchase .subtotal-purchase {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 30px 0 20px 0;
  border-top: 2px solid #605D5C;
}
div.minicart .purchase .subtotal-purchase span.subtotal {
  font-size: 1.25em;
  font-family: "Swiss 721 Bold";
  text-transform: uppercase;
  color: #000000;
}
div.minicart .purchase .subtotal-purchase strong.price {
  font-size: 1.25em;
  font-family: "Swiss 721 Bold";
  text-transform: uppercase;
  color: #A30810;
}
div.minicart .purchase .settle button {
  display: block;
  width: 100%;
  margin: 10px 0;
}
div.minicart .purchase .settle button.conclude {
  border: 3px solid #80C161;
  background-color: #80C161;
  border-radius: 30px;
  height: 50px;
  transition: 0.3s ease-in-out;
}
div.minicart .purchase .settle button.conclude:hover {
  background: #A30810;
  border-color: #A30810;
}
div.minicart .purchase .settle button.conclude a {
  display: block;
  width: 100%;
  height: 100%;
  line-height: 44px;
  text-transform: uppercase;
  color: #FFFFFF;
  font-family: "Swiss 721 Bold";
}
div.minicart .purchase .settle button.continue {
  border: 3px solid #A30810;
  background-color: transparent;
  border-radius: 30px;
  height: 50px;
  transition: 0.3s ease-in-out;
}
div.minicart .purchase .settle button.continue a {
  display: block;
  width: 100%;
  height: 100%;
  line-height: 44px;
  text-transform: uppercase;
  color: #FFFFFF;
  transition: 0.3s ease-in-out;
  color: #A30810;
  font-family: "Swiss 721 Bold";
}
div.minicart .purchase .settle button.continue:hover {
  background: #A30810;
}
div.minicart .purchase .settle button.continue:hover a {
  color: #FFFFFF;
}
div.minicart .title-cart {
  height: 70px;
  line-height: 70px;
  padding: 0 15px 0 15px;
  background: #A30810;
  text-transform: capitalize;
  text-align: left;
}
div.minicart .title-cart h2 {
  font-size: 1.25em;
  color: #FFFFFF;
  user-select: none;
  cursor: text;
}
div.minicart .main-content {
  height: calc(100% - 70px);
}
div.minicart .main-content div.inventory div.product {
  display: grid;
  grid-template-columns: 35% 1fr;
  align-items: flex-start;
  padding: 10px 0;
}
div.minicart .main-content div.inventory div.product div.product-image {
  margin-right: 15px;
}
div.minicart .main-content div.inventory div.product div.product-image a img {
  width: 100%;
}
div.minicart .main-content div.inventory div.product div.product-info {
  margin-left: 15px;
  text-align: left;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-details {
  margin: 15px 0;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-details span {
  font-size: 0.75em;
  color: #C4C4C4;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-change {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-change div.change-qty {
  width: 50%;
  display: inline-flex;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-change div.change-qty input[type=button] {
  width: 17px;
  height: 17px;
  border: none;
  outline: 0;
  text-align: center;
  background: #605D5C;
  color: #FFFFFF;
  border-radius: 5px;
  cursor: pointer;
  border: none;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-change div.change-qty input[type=number] {
  width: 30px;
  height: 17px;
  border: none;
  outline: 0;
  text-align: center;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-change div.change-qty input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-change div.change-qty input[type=number] {
  font-family: "Swiss 721 Bold";
  color: #605D5C;
  -moz-appearance: textfield;
  appearance: textfield;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-change div.info-price {
  width: 50%;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-change div.info-price strong {
  text-align: center;
  color: #FF0000;
  font-family: "Swiss 721 Bold";
  font-size: 1.125em;
}
div.minicart .main-content div.inventory div.product div.product-info div.info-name a {
  display: inline;
  font-family: "Swiss 721 Medium";
  font-size: 0.875em;
  line-height: 1.1em;
  color: #605D5C;
}
div.minicart .main-content div.inventory::-webkit-scrollbar-track {
  background-color: #F4F4F4;
}
div.minicart .main-content div.inventory::-webkit-scrollbar {
  width: 6px;
  background: #F4F4F4;
}
div.minicart .main-content div.inventory::-webkit-scrollbar-thumb {
  background: rgba(163, 8, 16, 0.2);
}

div.mobile div.minicart {
  z-index: 90;
  display: none;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  background: #FFFFFF;
  box-shadow: 0 5px 6px 0 rgba(0, 0, 0, 0.2);
  transform: translateX(999px);
  transition: 0.3s ease-in-out;
}
div.mobile div.minicart .close-minicart {
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(0);
  width: 50px;
  height: 70px;
  text-align: center;
  display: table;
  background: #A30810;
  z-index: 1;
}
div.mobile div.minicart .close-minicart strong {
  cursor: pointer;
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  color: #FFFFFF;
}
div.mobile div.minicart .main-content div.inventory {
  overflow: auto;
  padding: 15px;
  height: calc(100% - 198px);
}

/*==================================================
    =            Bootstrap 3 Media Queries             =
    ==================================================*/
/*==========  Mobile First Method  ==========*/
/* Custom, iPhone Retina */
/* Extra Small Devices, Phones */
/* Small Devices, Tablets */
@media only screen and (min-width: 768px) {
  html body header.header div.mobile {
    display: none;
  }
  html body header.header div.minicart .minicart-content .title-cart {
    text-align: center;
  }
}
/* Medium Devices, Desktops */
/* Large Devices, Wide Screens */
/*==========  Non-Mobile First Method  ==========*/
/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) {
  html body header.header div.desktop div.search {
    width: 30%;
  }
}
/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  html body header.header div.desktop {
    display: none;
  }
  html body header.header div.mobile {
    padding: 20px 15px;
    display: flex;
  }
}
/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  div.minicart .purchase .subtotal-purchase {
    padding: 15px 0 10px 0;
  }
  div.minicart .purchase .subtotal-purchase span.subtotal,
div.minicart .purchase .subtotal-purchase strong.price {
    font-size: 1.125em;
  }
  div.minicart .purchase .settle button.conclude,
div.minicart .purchase .settle button.continue {
    height: 45px;
  }
  div.minicart .purchase .settle button.conclude a,
div.minicart .purchase .settle button.continue a {
    line-height: 39px;
  }
}
/* Extra Small Devices, Phones */
/* Custom, iPhone Retina */
footer.footer {
  padding: 80px 0 30px 0;
}
footer.footer .wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 25%;
}
footer.footer .wrapper div.lists header {
  margin: 0 0 15px 0;
}
footer.footer .wrapper div.lists header h3 {
  color: #FF0000;
